import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class Errors extends Component {
    async loadAll() {
		this.setState({ isLoading: true });
		await this.loadErrors();
		this.setState({ isLoading: false });
    }

	loadErrors = async () => {
		const { adminStore } = this.props.stores;
        await adminStore.getErrors();
	}

    toggleLine = (e) => {
		const { idx } = e.target.closest('tr').dataset;
        const { showError = {} } = this.state;
        showError[idx] = !showError[idx];
        this.setState({ showError });
    }

    componentDidMount() {
        this.loadAll();
    }

    render() {
        const { showError = {} } = this.state;
        const { adminStore } = this.props.stores;
        const { errors } = adminStore;
        return (
            <div class='col-12'>
                <h5>Klient errors</h5>
                <div>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Date</th>
                                <th>User</th>
                                <th>Location</th>
                                <th>Error</th>
                            </tr>
                        </thead>
                        <tbody>
                            {errors.map((error, idx) => <>
                                <tr class='pointerCursor' onClick={this.toggleLine} data-idx={idx}>
                                    <td>{idx + 1}</td>
                                    <td class='text-overflow'>{util.isoDate(error.timestamp)}</td>
                                    {/* <td>{JSON.stringify(error.user, null, 4)}</td> */}
                                    <td class='text-overflow'>{error.user.email}</td>
                                    {/* <td><xmp>{JSON.stringify(error.location, null, 4)}</xmp></td> */}
                                    <td class='text-overflow'>{error.location.href}</td>
                                    {/* <td>
                                        <xmp>{JSON.stringify(error.error.name, null, 4)}</xmp>
                                        <xmp>{JSON.stringify(error.error.message, null, 4)}</xmp>
                                        <xmp>{error.error.stack.replace(/\\n/g, '\n')}</xmp>
                                    </td> */}
                                    <td>
                                        <strong>{error.error.name}:</strong> {error.error.message}<br />
                                        {showError[idx] && <xmp>{error.error.stack.replace(/\\n/g, '\n')}</xmp>}
                                    </td>
                                </tr>
                            </>)}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Errors;