import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class VeterinaryHistory extends Component {
    async loadAll() {
		this.setState({ isLoading: true });
		await this.getVeterinaryHistory();
		this.setState({ isLoading: false });
    }

	getVeterinaryHistory = async () => {
		const { adminStore } = this.props.stores;
        await adminStore.getVeterinaryHistory();
	}

    toggleLine = (e) => {
		const { idx } = e.target.closest('tr').dataset;
        const { showError = {} } = this.state;
        showError[idx] = !showError[idx];
        this.setState({ showError });
    }

    componentDidMount() {
        this.loadAll();
    }

    render() {
        const { showError = {} } = this.state;
        const { adminStore } = this.props.stores;
        const { veterinaryHistory } = adminStore;
        return (
            <div class='col-12'>
                <h5>Veterinary searches</h5>
                <div>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th>Date</th>
                                <th>Vet</th>
                                <th>Search</th>
                                <th>User</th>
                                <th>IP</th>
                                <th>UA</th>
                            </tr>
                        </thead>
                        <tbody>
                            {veterinaryHistory.map((vetLog) => <>
                                <tr>
                                    <td class='text-overflow'>
                                        {vetLog.date && <>
                                            {util.isoDate(vetLog.date)}
                                        </>}
                                    </td>
                                    <td class='text-overflow'>{vetLog.veterinary}</td>
                                    <td class='text-overflow'>{vetLog.searchText} ({vetLog.searchResults})</td>
                                    <td class='text-overflow'>{vetLog.user}</td>
                                    <td class='text-overflow'>{vetLog.ip}</td>
                                    <td class='text-overflow'>
                                        {vetLog.ua && <>
                                            {vetLog.ua.platform}, {vetLog.ua.os}, {vetLog.ua.browser} ({vetLog.ua.version})
                                        </>}
                                    </td>
                                </tr>
                            </>)}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default VeterinaryHistory;