import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import { toJS } from 'mobx'

import MiniPageViewGraph from '../../components/admin/miniPageViewGraph';

import mu from '../../lib/musher-util';

const initialState = {
	showAddComment: false,
};

@observer
class PageView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
		this.inputRef = {};
    }

	async loadPageView() {
		this.setState({ isLoading: true });
		const { adminStore } = this.props.stores;
        await adminStore.getPageViews();
		this.setState({ isLoading: false });
	}

	componentDidMount() {
		this.loadPageView();
	}

    render() {
        const { adminStore } = this.props.stores;
        const { pageViews, pageViewsKeys } = adminStore;

        let graphFieldsLeftFrontpage = [];
        let graphLegendsLeftFrontpage = [];
        let graphFieldsLeftRace = [];
        let graphLegendsLeftRace = [];
        let graphFieldsLeftSearch = [];
        let graphLegendsLeftSearch = [];
        let graphFieldsLeftInbox = [];
        let graphLegendsLeftInbox = [];
        let graphFieldsLeftSettings = [];
        let graphLegendsLeftSettings = [];
        let graphFieldsLeftUsers = [];
        let graphLegendsLeftUsers = [];
        let graphFieldsLeftDogs = [];
        let graphLegendsLeftDogs = [];
        let graphFieldsLeftWorkouts = [];
        let graphLegendsLeftWorkouts = [];
        let graphFieldsLeftTeams = [];
        let graphLegendsLeftTeams = [];
        let graphFieldsLeftTracks = [];
        let graphLegendsLeftTracks = [];

        // const pages = Object.keys(pageViews);
        const pages = pageViewsKeys.map(e => e.name);
        const graphKey = `pageViews-${pages.length}`;
        pages.forEach((key) => {
            if (key.match(/^(frontpage|feed|news|dashboard)/)) {
                graphFieldsLeftFrontpage.push(`pageViews.${key}`);
                graphLegendsLeftFrontpage.push(`${key}`);
            }
            if (key.match(/^(app-races)/)) {
                graphFieldsLeftRace.push(`pageViews.${key}`);
                graphLegendsLeftRace.push(`${key}`);
            }
            if (key.match(/^(search)/)) {
                graphFieldsLeftSearch.push(`pageViews.${key}`);
                graphLegendsLeftSearch.push(`${key}`);
            }
            if (key.match(/^(inbox)/)) {
                graphFieldsLeftInbox.push(`pageViews.${key}`);
                graphLegendsLeftInbox.push(`${key}`);
            }
            if (key.match(/^(settings)/)) {
                graphFieldsLeftSettings.push(`pageViews.${key}`);
                graphLegendsLeftSettings.push(`${key}`);
            }
            if (key.match(/^(user|todo|admin)/)) {
                graphFieldsLeftUsers.push(`pageViews.${key}`);
                graphLegendsLeftUsers.push(`${key}`);
            }
            if (key.match(/^dog/)) {
                graphFieldsLeftDogs.push(`pageViews.${key}`);
                graphLegendsLeftDogs.push(`${key}`);
            }
            if (key.match(/^workout/)) {
                graphFieldsLeftWorkouts.push(`pageViews.${key}`);
                graphLegendsLeftWorkouts.push(`${key}`);
            }
            if (key.match(/^team/)) {
                graphFieldsLeftTeams.push(`pageViews.${key}`);
                graphLegendsLeftTeams.push(`${key}`);
            }
            if (key.match(/^track/)) {
                graphFieldsLeftTracks.push(`pageViews.${key}`);
                graphLegendsLeftTracks.push(`${key}`);
            }
        });

        return (
            <>
                {/* <xmp>{JSON.stringify(pageViewsKeys, null, 4)}</xmp> */}
                <div class='w-100 mb-3'>
                    <h5>frontpage|feed|news|dashboard</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftFrontpage}
                        legendLeft={graphLegendsLeftFrontpage}
                        key={`frontpage-${graphKey}`}
                    />
                    <h5>app-races</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftRace}
                        legendLeft={graphLegendsLeftRace}
                        key={`race-${graphKey}`}
                    />
                    <h5>search</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftSearch}
                        legendLeft={graphLegendsLeftSearch}
                        key={`search-${graphKey}`}
                    />
                    <h5>inbox</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftInbox}
                        legendLeft={graphLegendsLeftInbox}
                        key={`inbox-${graphKey}`}
                    />
                    <h5>settings</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftSettings}
                        legendLeft={graphLegendsLeftSettings}
                        key={`settings-${graphKey}`}
                    />
                    <h5>user|todo|admin</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftUsers}
                        legendLeft={graphLegendsLeftUsers}
                        key={`users-${graphKey}`}
                    />
                    <h5>dog</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftDogs}
                        legendLeft={graphLegendsLeftDogs}
                        key={`dogs-${graphKey}`}
                    />
                    <h5>workout</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftWorkouts}
                        legendLeft={graphLegendsLeftWorkouts}
                        key={`workouts-${graphKey}`}
                    />
                    <h5>team</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftTeams}
                        legendLeft={graphLegendsLeftTeams}
                        key={`teams-${graphKey}`}
                    />
                    <h5>track</h5>
                    <MiniPageViewGraph stores={this.props.stores}
                        fieldsLeft={graphFieldsLeftTracks}
                        legendLeft={graphLegendsLeftTracks}
                        key={`tracks-${graphKey}`}
                    />

                </div>
            </>
        );
    }
}

export default PageView;
