import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class Development extends Component {
    setApiServer = (e) => {
		const { apiserver } = e.target.closest('button').dataset;
        const { appState } = this.props.stores;
        appState.setApiServer(apiserver);
    }

    render() {
        const { showError = {} } = this.state;
        const { appState } = this.props.stores;
        const { apiServer } = appState;
        return (
            <div class='col-12'>
                <h5>API Server: {apiServer}</h5>

                <button
                    class={`btn btn-block btn-${apiServer === 'https://themusher.app' ? '' : 'outline-'}success`}
                    onClick={this.setApiServer}
                    data-apiserver='https://themusher.app'
                >Prod</button>

                <button
                    class={`btn btn-block btn-${apiServer === 'https://dev.themusher.app' ? '' : 'outline-'}success`}
                    onClick={this.setApiServer}
                    data-apiserver='https://dev.themusher.app'
                >Dev</button>

                <button
                    class={`btn btn-block btn-${apiServer === 'http://localhost:8888' ? '' : 'outline-'}success`}
                    onClick={this.setApiServer}
                    data-apiserver='http://localhost:8888'
                >localhost:8888</button>
            </div>
        );
    }
}

export default Development;