import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class Development extends Component {
    setApiServer = (e) => {
		const { apiserver } = e.target.closest('button').dataset;
        const { appState } = this.props.stores;
        appState.setApiServer(apiserver);
    }

    copyToClipboard = (e) => {
        this.textArea.select();
        document.execCommand('copy');
        // This is just personal preference.
        // I prefer to not show the whole text area selected.
        e.target.focus();
        this.setState({ copySuccess: 'Copied!' });
    };

    render() {
        const { showError = {}, copySuccess } = this.state;
        const { appState } = this.props.stores;
        const { jwtToken, userEmail } = appState;
        return (
            <div class='col-12'>
                <h5>JWT Token: {userEmail}</h5>

                <textarea
                    class='form-control'
                    value={jwtToken}
                    rows={5}
                    onClick={this.copyToClipboard}
                    ref={c => this.textArea = c}
                />
                <small id='passwordHelpBlock' class='form-text text-success'>
                    {copySuccess}
                </small>

            </div>
        );
    }
}

export default Development;